<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-actions>
          <v-text-field label="Lsungswort:" :success="answered" clearable :rules="[validate]"></v-text-field>
        </v-card-actions>
        <v-card-actions>
          <v-btn :disabled="!answered" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from "vue";

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    headline: "Der Nether",
    unterkapitel1:
      'Sie willigte also ein und wir sprachen auf dem Weg zum Portal der Feuergeisterwelt ber alles was uns in den Sinn kam. Ihr Name war Shikari Feuergast. Ihr Vater war ein Chiblocker und Ihre Mutter eine Feuerbndigerin. Sie reiste viel durch die Welt und erkundete Stdte, schloss sich anderen Reisenden an und half Menschen wo sie nur konnte. Sie war gro, hatte Sommersprossen, kurze schwarze Haare und Wache grne Augen. "Wir sind da. Das Portal zur Feuergeisterwelt. Oder einfach gesagt der Nether, Wir sehen uns drben" Sie sah mich zgern und schubste mich in die wabernd lilane Wand. Mit nur einem blinzeln verschwand die grne Welt an diesem schnen Sommertag und wich einem dunklen Raum. Rote Wnde und Luft, die so hei war, dass sie beim Einatmen brannte wie Feuer, umgaben mich nun. Wieder wurde ich geschubst. "Komm schon. Wer hier trdelt, trdelt nie wieder." Sie grinste vergngt, packte meine Hand und zog mich hinter sich her. Meine Haut brannte am ganzen Krper. Schwei verdampfte bevor er die Poren verlie. Warum war es hier so unglaublich hei? Am Ende des Korridors ffnete sich eine riesige Hhle vor uns. Alles glhte orange. Dann begriff ich. Das glhen kam von einem Ozean aus Lava, der tief unter uns den Boden der Hhle ausfllte.Wir gingen einen Schmalen gepflasterten Weg entlang. Ich blickte zurck. Mein Krper wollte umdrehen. Flucht. So Hei. Alles Brennt... "Hey du schaffst das schon. Wir mssen nicht weit. Entfernungen sind hier schnell zu berwinden." Shikari legte mir den Arm um die Hfte. Meine Angst verflog und ich nahm einen tiefen schmerzhaften Atemzug. Ich sah einen schmalen Pfad, der hoch ber den Ozean aus Lava fhrte. "Bleib dicht bei mir." Sie ging vor und zog mich sanft hinterher. Der Pfad hatte immer mehr Lcken. "Spring mir hinterher. Du schaffst das! Gebt und nahezu mhelos sprang sie von einem Vorsprung zum anderen und ich folgte ihr.',
    }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "U0VFTEVOQlJBTkQ=") {
        this.answered = true;
      } else {
        this.answered = false;
      }

      return this.answered;
    }
  }
};
</script>

