<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-actions>
          <v-text-field label="Lsungswort:" :success="answered" clearable :rules="[validate]"></v-text-field>
        </v-card-actions>
        <v-card-text v-if="answered">{{ unterkapitel2 }}</v-card-text>
        <v-card-text v-if="answered">{{ unterkapitel3 }}</v-card-text>
        <v-card-actions>
          <v-btn :disabled="!answered" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from "vue";

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    headline: "Suche im Steinhaufen",
    unterkapitel1:
      '"Ok. Und nun die letzte Aufgabe." Scayos zog seine rmel zurck. "Finde die Armschienen. Ich habe sie gut versteckt." Diesmal schien sein grinsen sogar noch breiter. Er hatte seine Armschinen abgelegt. "Einen Tipp gebe ich dir. Sie sind nicht weit weg, aber ne Leiter wrde dir helfen sie zu finden."',
    unterkapitel2:
      '"Du bist mehr als wir alle hier erwartet hatten." Sagte Scayos anerkennend. "Nun geh und finde dein Schicksal. Und unter uns. Struktur in Tabellen ist das A und O fr ein erfolgreiches Leben. Wirklich!" Scrayos verabschiedete sich und ich verlies Kieselchen in Richtung Hafen.',
    unterkapitel3:
      'Ich hatte Glck. Schon auf dem Weg zum Pier sah ich ber den Fischerhtten die 3 Mste der Winds of Fire. Kapitn Rangox war noch nicht wieder aufgebrochen. Als ich die Planke erreicht hatte, grte mich ein Mannschafter. "Hallo Wasserbndiger. Wir haben auf dich gewartet. Wir sollen dich nach Republika bringen. Komm an Board. Wir legen sofort ab." "Captain Rangox. Der Passagier ist an Board!". Der Kapitn sah vom obersten Deck herab. "Wasserbndiger. Auf ein Wort!" Er winkte mich zu sich. Ich ging aufs oberste Deck. "Aye. Du sollst meinen Sohn, den Feuerlord gesehen haben. Sah er gesund aus?" Ich war verwundert. "Ihr Sohn ist Feuerlord Rango?  Ich habe ihn nur kurz gesehen und er hat nur mit einer Freundin von mir gesprochen. Er schien aber gesund." Kapitn Rangox griff sich an den Hinterkopf. "Danke. Es gibt nicht viele, die ich nach ihm fragen kann. Und wir sprechen schon lange nicht mehr. Ich war kein guter Vater. Aber ich bin auch kein schlechter Mensch. Wir bringen dich jetzt nach Republika. Die Order kam direkt vom Hinkelstein. Darf man fragen was so wichtig ist?" Er guckte mich ernst und durchdringend an. "Ich suche einen Schatz und der Rat vom Hinkelstein mchte mir dabei helfen." Ich formulierte extra drum herum, weil ich nicht mehr einschtzen konnte, was ich finden wrde. Was als harmlose Schatzsuche begann, fhlte sich mit jedem Schritt auf dieser Reise grer an.'
  }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "T0JTSURJQU4=") {
        this.answered = true;
      } else {
        this.answered = false;
      }

      return this.answered;
    }
  }
};
</script>

