</template>

<script>
import Vue from "vue";

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    headline: "Der Mond der Prinzessin",
    unterkapitel1:
      'Ich war berwltigt von den riesigen Gebuden aus Eis. Alles glnzte und schimmerte in allen Blautnen die man sich vorstellen kann. In dem kleinen Dorf, in dem ich aufwuchs, gab es nur kleine Iglus. Ein reges Treiben beschftigte die Stadt. Ich zog durch die Straen, immer in Richtung des Palasts, der erhaben ber die Dcher der Stadt ragte. Ich wusste nicht was ich genau suchte. Ich wrde wohl kaum einfach in den Palast spazieren um Fragen zu stellen. An einer Hauptstrae angekommen, kam ich nicht weiter. Eine groe Menschenmenge hielt sich an beiden Straenrndern auf. Auf der Strae fuhren langsam 3 alabasterfarbende Kutschen entlang. Aus dem Augenwinkel sah ich wie ein Kind vorne in den Reihen seinen Ball fallen lie, der dann direkt auf die Strae rollte. Als ich das Kind nach vorne springen sah, reagierte ich vllig kopflos. Ich machte einen riesigen Satz durch den Eisspeer, den ich unter mir wirkte. Es katapultierte mich knapp ber die Menschenmenge hinweg und direkt neben das Kind, das ich gerade noch zu fassen bekam, bevor es unter das Rad der Letzten Kutsche geraten wre. Die Kutsche stoppte abrupt. In einem Moment verstummte die ganze Strae. Die Tr der Kutsche ging auf. Ein Schleier silbrig weien Haares glitzerte so hell, dass ich das Gesicht der Person nicht erkennen konnte. "Danke. Komm zu mir in die Kutsche." Unglubiges Raunen ging durch die Menge, das Kind lief mit dem Ball zu seinem Vater und ich kletterte in die Kutsche.',
    unterkapitel2:
      '"Ist dir was passiert? Das war wirklich mutig!" Ich sa der Prinzessin der Wassernation gegenber. "Mir geht es gut. Eure Majestt." Sie lchelte vornehm. "Nenn mich Siara. Es gibt nicht viele Menschen die so selbstlos reagieren. Kann ich dir irgendwas geben fr deine Heldentat?" Das war meine Chance. Begeistert antwortete ich: "Ja da wre was! Kennst du das Relikt des ersten Wasserbndigers?" Sie grinste vergngt und fuhr sich mit ihrer zierlichen Hand durchs Haar. "Lass uns ein Spiel daraus machen. Ich erzhl dir was ich wei, wenn du mir sagen kannst wie das Pferd des Avatars heit."',
    unterkapitel3:
      '"Ja Super! Das ist richtig!" Sie zappelte kurz glcklich auf ihrer Sitzbank, bis sie sich zu errinnern schien wer sie ist. "OK, hr zu! Als Kind erzhlte mir meine Meisterin oft, dass vor langer zeit jede Prinzessin eine Tiara trug. Sie wurde von Geburt zu Geburt von Prinzessin zu Prinzessin weitergegeben. Sie war aus Weigold fein verarbeitet und trug einen gleiend weien Edelstein. Sie soll heilende Krfte verstrkt haben." Prizessin Siara senkte spielerisch die Stimme. "Doch irgendwann wurde die Tiara von Piraten gestohlen. Die Mondgttin war entsetzt von der Habgier und versenkte das Schiff ber einem uralten Tempel auf dem Grund des Meeres." So gebannt lauschte ich ihrer Stimme, dass ich ganz vergas wo ich war. Doch die Kutsche stopte und auch die Prinzessin schien aus ihrer eigenen Geschichte aufzuwachen. "Oh da haben wir uns verquatscht. Schn dich kennengelernt zu haben. Ich muss nun zum Unterricht. Geh du nach Tentacula und such dort nach dem besten Kapitain. Jeder seefahrende Wasserbndiger kennt diese Geschichte. Und wenn wir uns wieder sehen, darst du mir meine Tiara aufsetzen." Sie kicherte und stieg aus.'
  }),
