<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-text>{{ unterkapitel2 }}</v-card-text>
           <v-card-actions>
          <v-text-field label="Lsungswort:" :success="answered" clearable :rules="[validate]"></v-text-field>
        </v-card-actions>
        <v-card-text v-if="answered">{{ unterkapitel3 }}</v-card-text>
        <v-card-actions v-if="answered">
          <v-text-field label="Lsungswort:" :success="answered2" clearable :rules="[validate2]"></v-text-field>
        </v-card-actions>
        <v-card-actions>
          <v-btn :disabled="!answered2" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from "vue";

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    answered2: false,
    headline: "Astrales",
    unterkapitel1:
      "Ich berhrte die Symbole auf der Tr in der Reihenfolge. Sie leuchteten kurz lila auf und ein Zischen war zu hren. Die Tr ffnete sich und dahinter war eine alte Schriftrolle. Als ich sie ffnete erkannte ich, dass es eine Karte war. Sie war alt und verschnrkelt geschrieben. Die Rckseite war komplett vollgeschrieben. Wenn das eine Beschreibung fr den Weg war, wollte ich sie jetzt schon nicht lesen. Da htte sich der Autor echt mal krzer fassen knnen. Sagte ich zu Kero, der grinsend neben mir stand. Er antwortete spaend: Hier ist ein X auf der Karte. Geh doch einfach da hin. Schade. Ich dachte wir finden den Schal schon hier. Hopp Hopp. Mach dich auf den Weg.",
    unterkapitel2:
      'In diesem ruhigen Land des leise flsternden Windes, tief in den Dschungel gebaut, war eine der grten Stdte der Welt. Astrales war umgeben von einer riesigen Mauer, doch die Natur machte daran nicht Halt. Man hrte Affen schreien, Vgel balzen, Geparden brllen und Bltter rascheln. Die Gebude waren riesig und sahen zu Teilen selbst aus wie die umgebenen Bume. Hier musste ich Rast machen. Die Karte fhrte mich in den tiefsten Dschungel. Das Dickicht mit seinen giftigen Tieren und Pflanzen sollte man nicht unvorbereitet betreten. Zu viele Abenteurer haben schon nach den Schtzen alter Zivilisationen gesucht und sind nie zurckgekehrt. Ich betrat das Gasthaus, das einem Prunkvollen Waldanwesen glich und wurde sogleich vom Gastwirt begrt, der gerade sein Runde an den voll besetzten Tischen machte. Mit offenen Armen kam er auf mich zu. "Willkommen fremder Freund. Nenn mich Dreifan. Mir gehrt dieses bescheidene Gasthaus. Wie kann ich dich glcklich stimmen? "Ich brauche eine Machete, Proviant und ein Zimmer zum Schlafen. "Gut. Gut. Ein Abenteurer. Er sagte mir wo ich Essen und Ausrstung bekam und gab mir ein sehr schnes Zimmer. Ich Zeigte ihm noch meine Karte und er gab mir einen Hinweis. Dort wo das X auf der Karte verzeichnet war, soll es viele Hhlen geben. Mit diesem Wissen fhlte ich mich gut gewappnet und legte mich schlafen.',
    unterkapitel3:
      'Die Nacht war ruhig. Ich fhlte mich nach dem Schlaf wie neu geboren. Die Gerusche der Tiere und die khle Nachtluft haben mich schon am Abend zuvor total entspannt. Also Packte ich zusammen und machte ich mich auf durch die Stadttore. Ich verlie den befestigten Weg, rein ins dichte Unterholz, wo mich nur noch Karte und Kompass leiten konnten.'
      }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "TUFDSEVURQ==") {
        this.answered = true;
      } else {
        this.answered = false;
      }

      return this.answered;
    },
    validate2: function(input) {
      if (input == undefined) {
        this.answered2 = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "QkxBVFRMQVVTTU9TS0lUTw==") {
        this.answered2 = true;
      } else {
        this.answered2 = false;
      }

      return this.answered2;
    }
  }
};
</script>

