<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-actions>
          <v-text-field label="Lsungswort:" :success="answered" clearable :rules="[validate]"></v-text-field>
        </v-card-actions>
        <v-card-text v-if="answered">{{ unterkapitel2 }}</v-card-text>
        <v-card-text v-if="answered">
          <v-layout align-center column>
            <v-list>
              <v-list-tile style="height:35px" v-for="r in raetsel" :key="r">
                <i class="body-1">{{ r }}</i>
              </v-list-tile>
            </v-list>
          </v-layout>
        </v-card-text>
        <v-card-text v-if="answered">{{ unterkapitel3 }}</v-card-text>
         <v-card-actions v-if="answered">
          <v-text-field label="Lsungswort:" :success="answered2" clearable :rules="[validate2]"></v-text-field>
        </v-card-actions>
        <v-card-actions>
          <v-btn :disabled="!answered2" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from "vue";

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    answered2: false,
    headline: "Captain ISn0w",
    unterkapitel1:
      "Ein Pfad, kilometerweit geschlngelt ber den rauen Ozean. Gut, dass ich genug Proviant fr diese Wanderung dabei hatte. Immer nher kam ich den dampfenden Wassersulen, die mit so hohem Druck nach unten ins Meer schossen, dass sie eine ganze Stadt 100 Meter weit oben in der Luft trugen. Der Hafen unter der Stadt lag im wabernden Dampf der auf dem Ozean zerschellenden Wassersulen. Ich trieb mich ein wenig umher und ging zum grten Schiff, das ich finden konnte.",
    unterkapitel2:
      'Die Wachen vor der Planke lieen mich aber nicht zum Kapitn, also ging ich in die Hafenkneipe um einen Tee zu trinken und ein paar Gesprche zu fhren. Hinten in der Ecke, an einem runden Tisch sa ein Mann mit einem Br. Ich ging hinber und fragte nach dem besten Kapitn den er kennt. Er antwortete prompt und selbstsicher: "Er steht vor dir mein Freund. Captain Isnow nennt man mich!" Im Hintergrund spielte sicher seit einer halben Stunde das gleiche Lied. Er sah erfahren aus, wirkte aber bermig stark von sich berzeugt und redete stndig mit seinem Bren, der jedoch immer nur blkte. Darum fragte ich: "Habt ihr denn auch ein Schiff?" "Ja. Das schnellste das je gebaut wurde. Aber kannst du es dir auch leisten?" Geld hatte ich kaum, also fragte ich nach einer Karte zum versunkenen Tempel. "Den Tempel der Mondgttin, nennt man ihn hier. Fr kein Geld der Welt wrde ich an diesen Ort fahren. Ich mag dich, also gebe ich einer armen Seele einen Tipp. Damit du ihn findest, falls er existiert. Ein Teil der Legende ist ein Sprichwort."',
    raetsel: [
      '"Der Mond geht auf, doch nicht zu Land.',
      "Der Mond geht unter, auf Grund der See.",
      "Der Weg durchs Wasser im Dunklen beschrieben.",
      "Grnes Schimmern.",
      "Sanftes leuchten.",
      'Finde den Weg unter Bgen im tiefen Blau."'
    ],
    unterkapitel3:
      'ISn0w zuckte lssig mit den Schultern. "Also wenn du mich fragst, klingt das verdchtig nach der Unterwasserstadt Serenity Valley. Du! Wenn du dabei drauf gehst, beschwer dich nicht bei mir!"'
  }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "U0NISUZG") {
        this.answered = true;
      } else {
        this.answered = false;
      }

      return this.answered;
    },
    validate2: function(input) {
      if (input == undefined) {
        this.answered2 = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "U0VSRU5JVFk=") {
        this.answered2 = true;
      } else {
        this.answered2 = false;
      }

      return this.answered2;
    }
  }
};
</script>

