<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-text>{{ unterkapitel2 }}</v-card-text>
        <v-card-text>{{ unterkapitel3 }}</v-card-text>
        <v-card-actions>
          <v-text-field label="Lsungswort:" :success="answered" clearable :rules="[validate]"></v-text-field>
        </v-card-actions>
        <v-card-actions>
          <v-btn :disabled="!answered" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from "vue";

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    headline: "Lavandia",
    unterkapitel1:
    'Ich sah das Portal und fing sofort an zu rennen. Ich musste raus. Ein blinzeln und ich stand in einem Haus aus Sandstein. Die Hitze verflog. Es war geschafft. Ich machte einen Schritt zur Seite und Shikari erschien aus dem Portal mit den Hnden nach vorn gestreckt. "Oh" Sagte sie verwundert und lachte. "Da war garkeiner zum schubsen". Ich lachte auch. Quatschend liefen wir in der Dunkelheit auf eine groe Pyramide zu, die mit leuchtenden Steinen geschmckt war. Die Wege die wir nach einiger Zeit erreichten waren nicht wie sonst von Straenlaternen oder Feuerkferlampen beleuchtet. Stattdessen hatte man an beiden Seiten Streifen aus Flammengestein eingelassen, die nachts angeheizt von der Sonnenstrahlung des Vortages glhten. Je nher wir der Pyramide kamen, desto mehr Soldaten hielten sich an den Wegesrndern auf. "Das Rtsel wovon ich dir erzhlt habe... Hast du eine Idee dazu?" Fragte ich Shikari. Sie nickte. "Naja du brauchst 4 Schlsselwrter, die du im Nether nutzen kannst, um einen Ring zu bekommen. Und die sind in 4 Stdten der Feuernation versteckt. Das ist doch ganz einfach." "Jaaa so hnlich htte ich das auch gedacht" Antwortet ich etwas verlegen. Sie grinste hmisch. "Ist ja nicht so, als wrst du der Erste der nach dem Sonnenring sucht. Der Walter von dem du das Tagebuch hast, hat meinen Vater damals das gleiche gefragt. Was aus Ihm geworden ist, wei ich aber nicht." Mir stockte der Atem und entsetzt purzelten mir die Worte aus dem Mund. "Warum sagst du das nicht gleich?" Sie antwortete nur: "Du hast nicht gefragt."',
     unterkapitel2: 'Am Fu der Pyramide angekommen, stand eine Gruppe hochrangiger Feuerbndiger im Weg und schien auf jemanden zu warten. Ich ging zur Seite, doch Shikari lief genau auf sie zu. Ich blieb stehen und traute meinen Augen nicht, denn nun fiel mir auf, dass der Mann in der Mitte die Krohne der Feuernation trug. Es war Feuerlord Rango. "General Feuergast, haben Sie die Informationen an das Luftknigreich berbracht?" Shikari antwortete. "Ja Feuerlord Rango hier ist die Antwort." Sie holte die versiegelte Schriftrolle hervor, die sie vllig unbeachtet von mir, an ihrem Grtel trug. "Ich bin mit diesem Wasserbndiger angereist." Sie nickte zu mir hinber und ich nahm unwillkrlich Haltung an und entgegnete: "Ja so ist es euer Majestt." Feuerlord Rango sah mich mit durchdringendem Blick an und wandte sich dann wieder Shikari zu. "Gut. Ich erwarte ihren Bericht zum Sonnenuntergang." Die Delegation machte sich dann wieder auf den Weg in die Pyramide.',
     unterkapitel3: '"Los lass uns Schtze suchen." Sagte Shikari vergngt, als die Gruppe weit genug entfernt war. "General Feuergast?" Fragte ich sie vllig verwirrt. Sie lachte laut auf. "Fr dich nur Shikari." Und unsere Suche begann.',
   
     }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "QUxUQVIgREVSIEZFVUVSUklOR0U=") {
        this.answered = true;
      } else {
        this.answered = false;
      }

      return this.answered;
    }
  }
};
</script>

