<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-text>{{ unterkapitel2 }}</v-card-text>
        <v-card-actions>
          <v-text-field label="Lsungswort:" :success="answered" clearable :rules="[validate]"></v-text-field>
        </v-card-actions>
        <v-card-actions>
          <v-btn :disabled="!answered" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from "vue";

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    headline: "Yama no Kaze",
    unterkapitel1:
      "Auf dem Weg in die Stadt fhlte ich die Ruhe die diese Region ausstrahlte. Es war als wrde die Seele Urlaub machen. Ich vergas den Schmerz in meinen Fen und die Anstrengungen, die ich fr mein Abendteuer auf mich nahm. Als ich am Ende eines Tunnels ankam, traute ich meinen Augen nicht. Da schwebte wirklich eine ganze Stadt am Himmel. Sie war anscheinend gerade dabei sich dem Anleger am Berg zu nhern. So ein Glck wusste ich. Nach allem was ich darber gehrt hatte, ist die Stadt wohl nur 2 von 14 Tagen erreichbar. Den Rest der Zeit war sie wohl in anderen Teilen der Welt unterwegs.",
    unterkapitel2:
      'Als ich an den Toren der Stadt angekommen war, flog sogar ein Bison in einiger Entfernung vorbei. In der Stadt glitten Kinder in einem Spiel umher. Hey Vorsicht! Eine Hand packte mich an der Schulter und zog mich zurck, whrend vor mir auch schon eins der Kinder entlangzischte. Geh nicht aufs Spielfeld. Entschuldige. Hallo, ich bin Kero. Du bist nicht von hier. Ein Wasserbndiger? Was bringt dich zu uns? Ich erzhlte ihm von meiner Reise und er guckte mich prfend an. Das ist kann ein gefhrliches Unterfangen sein. Aber wenn du schon die Tiara des Mondes hast, traue ich dir das zu. Hier bei uns in der Stadt gibt es eine verschlossene Tr, die nur aufgeht, wenn man sich wrdig erweist. Erkunde die Stadt nach belieben und lse das Rtsel. Ich ffne dir jede Tr. Du suchst den "Schal der Wolken".'
  }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "S09MSUJSSQ==") {
        this.answered = true;
      } else {
        this.answered = false;
      }

      return this.answered;
    }
  }
};
</script>

