<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-text>{{ unterkapitel2 }}</v-card-text>
        <v-card-text>{{ unterkapitel3 }}</v-card-text>
        <v-card-actions>
          <v-text-field label="Lsungswort:" :success="answered" clearable :rules="[validate]"></v-text-field>
        </v-card-actions>
        <v-card-actions>
          <v-btn :disabled="!answered" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from "vue";

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    headline: "Der Nether. wieder.",
    unterkapitel1:
      "Shikari wirkte garnicht wie ein General. Sie war quirlig und herzlich. Der Sonnenschein in Person. Und doch wirke sie nun ein bisschen gefhrlich.",
    unterkapitel2:
      'Wir standen wieder vor dem lilanen Portal "Komm. Die Stelle im Nether, wo wir die Wrter brauchen kenne ich." Nochmal in den Nether zu gehen, war das schlimmste, was ich mir vorstellen konnte. "Ich schaff das nicht nochmal aus eigener Kraft." sagte ich, stellte mich nahe an das Portal und fragte sie:"Hilfst du mir mal?" Mit breitem Grinsen entgegnete sie: "Nichts lieber als das." Und schubste mich durch das Portal.',
    unterkapitel3:
      'Nach den Qualen des Nethers war es so weit. Ich hatte nun 3 von 4 Relikten. Shikari freute sich fr mich und ber die gelungene Schatzsuche. "Wenn du den Ring nicht mehr brauchst, dann komm wieder zu mir. Wir bergeben ihn dann gemeinsam an Feuerlord Rango. Dann wirst du sicher auch zum General ernannt." Scherzte sie. Ich stimmte zu und schlug ihr vor, dass wir, als das tolle Team, das wir darstellten, auch das Relikt der Erdbndiger zusammen finden knnten. "Nein, das geht leider nicht." Antwortete sichtlich betroffen. "Ich habe Pflichten als General... Wenn man will, dass die Welt friedlich ist, mssen friedliche Menschen Verantwortung bernehmen. So habe ich alles gegeben um General zu werden und werde alles geben um es zu bleiben. Fr den Frieden." Langsam begriff ich wer sie war. "Danke. Fr alles." Ich nahm sie zur Verabschiedung in den Arm und machte mich dann auf den Weg zum nchsten Hafen.'
  }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "U09OTkVOUklORw==") {
        this.answered = true;
      } else {
        this.answered = false;
      }

      return this.answered;
    }
  }
};
</script>

