<script>
import Vue from 'vue';

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    answered2: false,
    headline: "Kieselchen",
    unterkapitel1: '"Captain Rangox wir haben vertut! " Der erste Mat machte seine Meldungen und gab Befehle an die Crew. Hektik war an Deck ausgebrochen und weckte mich. Wir waren im Hafen der Erdnation angelangt und die Handelsgter wurden gerade an Deck geholt. Ich verstaute meine Habe und machte mich auf den Weg. ',
    unterkapitel2: 'Raus aus der Hafenstadt, ber Berg und Tal. Die Groe Strae der Erdnation verband die Hafenstadt mit dem weit im Inland liegenden Kieselchen. Nie zuvor war ich auf einer so breiten und makellosen Strae unterwegs gewesen. So weit man sehen konnte, fand man auf dieser Strae weder ein Schlagloch, noch einen festgetretenen Kaugumfelsen(Kaugummi). Nach langem bewandern und bewundern der Strae kam ich in ein kleines Tal zwischen steilen Felswnden. Groe Tore waren fein eingearbeitet in die Felswnde. Eines der Tore stand offen und lies tief in eine hell erleuchtete Halle blicken. Ich lief durch das Tor und blickte mich um. Die Halle war zwar recht gro, aber bei weitem nicht so beeindruckend, wie die Hndler auf dem Marktplatz immer erzhlten. Ich ging weiter hinein. Breite Treppen fhrten immer tiefer ins Massiv und wurden mehrmals von weiteren Ebenen Unterbrochen. Menschen drngelten sich dicht an dicht auf den Treppen. Als ich die dritte Ebene betrat, blieb ich einen Moment im Gedrnge stehen und reckte den Kopf staunend nach oben. Die ganze Zeit die ich nun auf den Treppen und in den kleinen Hallen auf dem Weg nach unten verbrachte, verbrachte ich nur im Eingangsbereich. Ich stand auf einer Plattform in einer hell erleuchteten Hhle. berall um mich herum sah ich andere schwebende Plattformen, die nur durch Treppen und ranken des Bewuchses verbunden waren. berall waren Hndler, Musikanten, im Gras liegende Menschen. Ein Wasserfall fiel von Plattform zu Plattform und dann runter in ein Nichts, das tiefer schien, als ich je gesehen hatte. Pltzlich packte mich jemand hart an der Schulter und zog mich beiseite. "Hey was soll das?" fragte ich aufgebracht vor Schreck. "Entschuldige." Ein breites versteinertes Grinsen strahlte mich an. "Willkommen in den hngenden Grten von Kieselchen. Wir haben dich erwartet." Sagte der grinsende junge Mann. Er trug eine locker sitzende Lederrstung und eine Spitzhacke am Grtel, die kunstvoll den Schriftzug "Mending is Love" auf dem Griff eingraviert hatte. Die Frage wozu man als Erdbndiger eine Spitzhacke brauchte verkniff ich mir lieber. "Wer bist du und was heit wir haben dich erwartet?" fragte ich noch immer etwas argwhnisch. "Ich bin Scrayos." Er zog beilufig eine Nagelschere aus der Tasche und knipste akribisch ein hervorstehendes Blatt an einem Busch ab. "Wo war ich? Ach ja. Wir sind die Mitglieder des Hinkelsteinbndnisses. Wir beobachten dich jetzt schon lnger. berall wo der Wind den Staub am Boden aufwirbelt, haben wir unsere Augen und Ohren. Aber wir wollen dir nichts Bses. Wir wollen dich dein Ziel erreichen sehen. Natrlich mssen wir dich vorher prfen. Also bitte folge mir." Er drehte sich um und ging voraus. Ich war etwas berfordert, folge ihm aber, da ich nicht das Gefhl hatte eine Wahl zu haben.',
    unterkapitel3: 'Wir kamen auf eine Plattform, die weit oben schwebte und zu der nur eine gut bewachte Hngebrcke fhrte. An allen Seiten der Plattform lief Wasser hinunter, das aus den am Rand aufgestellten Blumentpfen zu kommen schien. In der Mitte war ein groer Tisch der Kunstfertig aus einem einzigen Stein gehauen war. "Setz dich" sagte Scrayos und bot mir einen Steinhocker an. An dem Tisch saen bereits einige wichtig aussehende Gesichter in feinster Kleidung und Kero. Scrayos stellte sich hinter seinen Stuhl und alle wandten sich ihm zu. "Willkommen zur Teambesprechung am Hinkelstein. Wir haben heute einen speziellen Gast. Er versucht das Geheimnis von Republika zu lsen. Dazu braucht er meine Armschienen. Wir sind hier um abzustimmen, ob er wrdig ist die Schienen des Unbndigen zu tragen." "Prfen wir zuerst seinen Geist!" rief ein lterer Mann in die Runde. Alle klopften zustimmend auf den Tisch. "Eine Prfung des Geistes also." fuhr Scrayos fort. "Gut. So sei es. Zuerst finde 6 mal 6 Zeichen in dieser Stadt. Dann sammle 6 Hinweise und bilde aus 36 Zeichen 6 richtige. Das Spiel heit Mastersteind. Dann komm wieder zu uns und wir sagen dir ob du bestanden hast.'
    }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "SElOS0VMU1RFSU4=") {
        this.answered = true;
      } else {
        this.answered = false;
      }
      
      return this.answered
    },
    validate2: function(input) {
