<template>
  <v-flex xs8 offset-xs2>
    <v-layout justify-center>
      <v-card flat max-width="400">
        <v-card-title>
          <h2 class="headline">{{ headline }}</h2>
        </v-card-title>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-text>{{ unterkapitel2 }}</v-card-text>
        <v-card-text>{{ unterkapitel3 }}</v-card-text>
        <v-card-actions>
          <v-text-field
            label="Lsungswort:"
            :success="answered"
            clearable
            :rules="[validate]"
          ></v-text-field>
        </v-card-actions>
        <v-card-text v-if="answered">{{ unterkapitel4 }}</v-card-text>

        <v-card-actions>
          <v-btn :disabled="!answered" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from 'vue';

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    headline: "Suche nach Hinweisen",
    unterkapitel1:
      'Ich schlug das Buch auf und fing an zu lesen. Walter schrieb auf den ersten Seiten viel alltgliches. Doch auf der siebten Seite fand ich es. Er sprach an diesem Tag mit einem leicht gebckt gehenden fremden vor der Bibliothek, der ihn nach dem Weg zur Feuernation fragte. Walter empfahl ihm das Boot von Captain Rangox im Hafen. Er wrde umsonst mitfahren drfen, wenn er sagte wer ihn schickt. Der Fremde sagte zu Walter: "Ich habe nichts was ich dir fr deine Freundlichkeit geben kann und doch gebe ich dir das wertvollste was ich besitze. Ein kleines Stck Wissen, das vergessen ist und bald mit mir gestorben wre. Republika hat ein Geheimnis. Gut versteckt und schwer zu finden. Wie das Wissen selbst ist es mchtiger als jedes Schwert. Geh in die Bibliothek und suche nach dem Buch der 4 Elemente. Es wird dich leiten."',
    unterkapitel2: 'Ich schlug das Tagebuch zu und blickte auf. Der Verkufer des Standes guckte mich streng an. "Wenn du weiterlesen willst, musst du das Buch schon kaufen. Ohne zu verhandeln gab ich ihm was er verlangte und machte mich auf den Weg zur Bibliothek.',
    unterkapitel3: 'In der Bibliothek angekommen, suchte ich nach dem Buch',
    unterkapitel4: 'Nachdem ich es in der Bibliothek in Republika nicht finden konnte, hatte ich mich nach Fantastika begeben. Und da war es. Das Buch der 4 Elemente. Schwer und wunderschn verziert fragte ich mich, ob ich es berhaupt aus der Bibliothek mitnehmen drfte. Ich ging ins Erdgeschoss zum Schalter, vorbei an riesigen regalen voll mit allem Wissen ber die Wunder der Welt. ber die Brille guckte mich ein Mdchen mit glattem schwarzen Haar an. Sie war gerade damit beschftigt Bcher in Listen einzutragen. Im leicht geflsterten, aber bestimmten Ton fragte sie: "Was mchtest du?" Leicht schchtern antwortete ich: "Dieses Buch mchte ich leihen." Sie grinste mich an. "Geh zu Tobi. Von den schnsten Bchern hat er Abschriften gefertigt. er gibt dir eine mit. Diese Bcher sind nicht verleihbar." Man sah eine liebvolle Begeisterung in ihren Augen. Ich stammelte noch ein "Danke." raus und ging ins Bro auf das sie zeigte.'
    }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "UkVMSUtURQ==") {
        this.answered = true;
      } else {
        this.answered = false;
      }
      
      return this.answered
    }
  }
};
</script>

