<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
                <v-card-text>
          <v-layout align-center column>
            <v-list>
              <v-list-tile style="height:35px" v-for="r in raetsel" :key="r">
                <i class="body-1">{{ r }}</i>
              </v-list-tile>
            </v-list>
          </v-layout>
        </v-card-text>
        <v-card-text>{{ unterkapitel2 }}</v-card-text>
        <v-card-actions>
          <v-text-field label="Lsungswort:" :success="answered" clearable :rules="[validate]"></v-text-field>
        </v-card-actions>
        <v-card-actions>
          <v-btn :disabled="!answered" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from "vue";

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    headline: "Die Reisende",
    unterkapitel1:
      "Das schier endlose Treppengehen ging wie von selbst, also las ich ein wenig. Das nchste Kapitel im Buch der 4 Relikte handelte von der Feuernation. Eine feindseelige Region. Karge Lndereien, brodelnde Lavaseen, verdorrte Pflanzen, riesige Wsten und strenge Menschen. Doch trotzdem freute ich mich. Nirgendwo sonst gab es so viele Badehuser an heien Quellen mit erfrischenden Getrnken. Wie erwartet fand ich wieder einen Spruch.",
    raetsel: [
      "Feuer, Hitze, heier Sand.",
      "Hier versteckt mit viel Verstand.",
      "Vier Worte, vier Orte sind fr dich Garant.",
      "Findest was dein Herz erkannt,",
      "das Unheil nur mit ihm verbannt,",
      "ein Ring aus Lava in deine Seele eingebrannt."
    ],
    unterkapitel2:
      'Unten angekommen, sah ich eine reisende Feuerbndigerin. Ich erkannte sie an ihrer Kleidung. Sie hatte ein freundliches Gesicht und sah gut gelaunt aus, also sprach ich sie an. "Hallo feurige Schnheit, knnen Sie mir den schnellsten Weg nach Lavandia nennen? Sie lachte laut auf. "Feurige Schnheit? Du bist ja s. Ich kann dir den Weg durch den Nether verraten. Dann bist du im nu da. sie grinste. "Oder tot. Antwortete ich lachend, mit allem Galgenhumor den ich aufbringen konnte. Sie fuhr fort: "Du kannst natrlich auch ein Schiff nehmen, du cooler Wasserkrieger. Aber wenn ich als zierliches Mdchen durch den Nether reise, was macht das dann aus dir? Ich mochte ihre provokante Art und entgegnete. "Da hast du natrlich recht. Damit knnte ich nicht leben. Ich wei ich werde es bereuen, aber willst du mir den Weg zeigen? "Klar knnte ich das. Wenn du mir sagen kannst, wie das Feuerbndigen entstanden ist.'
  }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "RFJBQ0hFTg==") {
        this.answered = true;
      } else {
        this.answered = false;
      }

      return this.answered;
    }
  }
};
</script>

